import { Component, OnInit } from '@angular/core';
import { TranslateService } from 'ng2-translate';
import { Router } from '@angular/router';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import {HeaderStateService,HEADER_STATE} from '../../services/header-state.service';
import {SharedModelService} from '../../services/shared-model.service';
@Component({
  selector: 'app-account-type',
  templateUrl: './account-type.component.html',
  styles: []
})
export class AccountTypeComponent implements OnInit {
  /**
   * @var {array} languages
   * @var {string} selectedLangguage
   */
  languages = [{ label: 'Français', value: 'fr' }, { label: 'English', value: 'en' }]
  selectedLangguage = 'fr'

  /**
   * @constructor
   * Constuctor of AccountTypeComponent class
   * @param {Router} router the router
   * @param {HeaderStateService} _headerSteps service
   * @param {SharedModelService} _sharedModelService model object
   * @param {FillDataStepsChangerService} _fillDataStepService service
   */
  constructor(private translateService: TranslateService,
              private router: Router, 
              private _headerSteps:HeaderStateService,
              private _sharedModelService:SharedModelService,
              private _fillDataStepService: FillDataStepsChangerService) { }


/**
 * Initialisations
 * @function ngOnInit
 */
  ngOnInit() {
    this._fillDataStepService.setCurrentStep({step:FILL_DATA_STEPS.INFORMATION,subStep:0});
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);
  }

/**
 * Change the current language
 * @function onLanguageChange
 * @param {string} val 
 */
  onLanguageChange(val) {
    this.translateService.use(this.selectedLangguage);
  }

/**
 * Specify type of the account
 * @function individualAccount
 */
  individualAccount() {
    this._sharedModelService.model.accountType = 'indivudualAccount';
    this.next()
  }

/**
 * Move to the next step
 * @function next
 */
  next() {
    this.router.navigate(['../personal-information'])
    window.scrollTo(0,0)
  }

/**
 * Assign the type of the account and move to the next step
 * @function jointAccount
 */
  jointAccount() {
     this._sharedModelService.model.accountType = 'jointAccount';
    this.next()
  }

}



