import { Component, OnInit } from '@angular/core';
import { TranslateService } from 'ng2-translate';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import { HeaderStateService, HEADER_STATE } from '../../services/header-state.service';
import {SharedModelService} from '../../services/shared-model.service';
@Component({
  selector: 'app-credit-card',
  templateUrl: './credit-card.component.html',
  styleUrls: ['./credit-card.component.css']
})
export class CreditCardComponent implements OnInit {

/**
   * @var {string} cardType
   * @var {string} showAdvantage
   * @var {string} debitCreditCard
   */
  cardType: string;
  showAdvantage: string;
  debitCreditCard: string = 'dd';
  checked = false;
  /**
   * @constructor 
   * Constuctor of CreditCardComponent class
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {HeaderStateService} _headerSteps
   * @param {SharedModelService} _sharedModelService Model service
   * @param {FillDataStepsChangerService} _fillDataStepService
   */
  constructor(private router: Router, private _location: Location,
    private _headerSteps: HeaderStateService,
    private _sharedModelService: SharedModelService,
    private _fillDataStepService: FillDataStepsChangerService) { }

/**
 * Initialisations
 * @function ngOnInit
 */
  ngOnInit() {
    this._fillDataStepService.setCurrentStep({step:FILL_DATA_STEPS.ACCOUNT,subStep:2});
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);
  }

  /**
 * Move to the previous step
 * @function goBack
 */
  goBack() {
    this._location.back()
    window.scrollTo(0,0)
  }

/**
 * Move to the next step
 * @function next
 * @param {string} type The type of the credit card
 */
  next(type) {
    this._sharedModelService.model.informations.account.creditCard.type = type;
    this._sharedModelService.model.informations.account.creditCard.debitType = this.debitCreditCard;
    this.router.navigate(['card-utilisation']);
    window.scrollTo(0,0)
  }

  closeAdvantages() {
    this.showAdvantage = "";
  }

/**
 * Move to the next step
 * @function next
 * @param {FillDataStepsChangerService} event The event service
 * @param {string} type The type of the credit card
 */
  showAdvantageOf(event,type){
    event.stopPropagation();
    this.showAdvantage = type;
    this.cardType = type;
  }

  creditCardType(type) {
    this.cardType = type;
    this.showAdvantage = null;
  }

}



