import {
  Component,
  OnInit
} from '@angular/core';
import {
  Location
} from '@angular/common';
import { Router } from '@angular/router';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import {HeaderStateService,HEADER_STATE} from '../../services/header-state.service';
import {CountriesDataService} from '../../services/countries-data.service';
import {SharedModelService} from '../../services/shared-model.service';
import {UsefulDataService} from '../../services/useful-data.service';
import {TranslateService} from 'ng2-translate';
@Component({
  selector: 'app-personal-information-second-page',
  templateUrl: './personal-information-second-page.component.html'
})
export class PersonalInformationSecondPageComponent implements OnInit {

  /**
   * @var {boolean} martialStatusClosed
   * @var {array} longNumbers
   * @var {array} nationalities
   * @var {array} countries
   * @var {array} martialStatus
   * @var {array} familySituations
   */
  martialStatusClosed = false;
  longNumbers = [];
  nationalities = [];
  countries = [];
  martialStatus = [];
  familySituations = []
  sharedModelService: SharedModelService;

  /**
   * @constructor
   * Constructor of PersonalInformationSecondPageComponent class
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {HeaderStateService} _headerSteps
   * @param {TranslateService} _translate
   * @param {UsefulDataService} _usefuldata
   * @param {CountriesDataService} _countriesService
   * @param {SharedModelService} _sharedModelService
   * @param {FillDataStepsChangerService} _fillDataStepService 
   */
  constructor(private _location: Location, 
              private router: Router, 
              private _headerSteps:HeaderStateService,
              private _translate: TranslateService,
              private _usefuldata: UsefulDataService,
              private _countriesService: CountriesDataService,
              private _sharedModelService: SharedModelService,
              private _fillDataStepService: FillDataStepsChangerService) { 
                this.sharedModelService = this._sharedModelService;
              }

/**
 * Initialisations
 * @function ngOnInit
 */
  ngOnInit() {
    this._fillDataStepService.setCurrentStep({step:FILL_DATA_STEPS.INFORMATION,subStep:2});
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);
    this._countriesService.getCountries().then((countries)=>{
      this.countries = countries;
    })
    this.updateForm();
    this._translate.onLangChange.subscribe((ev)=>{
      this.updateForm();
    })
  }

  updateForm(){
     this.familySituations = this._usefuldata.getFamilySituations(this._translate.currentLang);
      this.martialStatus = this._usefuldata.getMartialStatus(this._translate.currentLang);
      this.longNumbers = this._usefuldata.getLongNumbersName(this._translate.currentLang);
  }

  onPostalCodeChange(event) {
    if(event.value === undefined)
      console.log("undefined")
    else
      this._sharedModelService.model.informations.person.validateZipCode(event.value);
  }

  /**
   * Move to the previous step
   * @function goBack
   */
  goBack() {
    this._location.back();
     window.scrollTo(0,0)
  }

  /**
   * delete last selected nationality
   * @function deleteLastNationality
   */
  deleteLastNationality() {
   this._sharedModelService.model.informations.person.otherNationalities.pop();
  }

  /**
   * Move to the next step
   * @function next
   */
  next() {
    if(this._sharedModelService.model.informations.person.validateInformationsSecondPage()){
      this.router.navigate(['/coordonees']);
      window.scrollTo(0,0)
    }
     
  }

  /**
   * Add anothernationality
   * @function AddOtherNationality
   */
  AddOtherNationality() {
    this._sharedModelService.model.informations.person.otherNationalities.push({
      name: null
    });
  }
}




