const REQUIRED = "Ce champs est obligatoire";
const INVALID = "La valeur entrée est invalide"
export class Model {
    accountType: string;
    informations = new Informations();
}

class Informations {
    person = new PersonalInformation();
    contactDetail = new ContactDetails();
    account = new Account()
}
class PersonalInformation {
    //First form 
    civility: string = 'mo';
    firstName: string;
    firstNameError: string;
    lastName: string;
    lastNameError: string;
    birthDate: Date;
    birthDateError: string;
    email: string;
    emailError: string;
    mobilePhoneDialCode: string = '+33';
    mobilePhone: string;
    mobilePhoneError: string;
    contactMeByEmailOrSMS: boolean = true;

    //second form
    familySituation: string = "single";
    maritalStatus: string = "r1";
    birthName: string;
    birthNameError : string;
    birthCountry: string = 'FR';
    zipCode: string;
    zipCodeError: string;
    nationality: string = 'FR';
    otherNationalities = [];
    isUsPerson: string = 'isUsPerson';
    shouldValidateMartialStatus = true;
    initErrors() {
        this.firstNameError = null;
        this.lastNameError = null;
        this.emailError = null;
        this.birthDateError = null;
        this.mobilePhoneError = null;
    }
    validateEmail(email) {
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(email);
    }
    validatePhone(phone) {
        let re = /^[+]*[(]{0,1}[0-9]{1,3}[)]{0,1}[-\s\./0-9]*$/g
        return re.test(phone);
    }
    validateFirstName(firstName) {
        if (firstName.trim().length == 0) {
            this.firstNameError = REQUIRED
        } else {
            this.firstNameError = null
        }
    }
    validateLastName(lastName) {
        if (lastName.trim().length == 0) {
            this.lastNameError = REQUIRED
        } else {
            this.lastNameError = null
        }
    }
    validateDate(date){
        if(!date){
            return false
        }
        let timeUNix = date.getTime()
        let timeUnixSince1900 = new Date('01/01/1900').getTime()
        let timeUNixSinceNow = new Date().getTime()
        return timeUNix > timeUnixSince1900 && timeUNix < timeUNixSinceNow
    }

    initBirthDateError(){
        this.birthDateError = null;
    }

    dateValidationErrorMessage(isValid) {
        if(!isValid) {
            this.birthDateError = INVALID;
        } else if (!this.validateDate(this.birthDate)) {
            if (!this.birthDate) {
                this.birthDateError = REQUIRED;
            } else {
                this.birthDateError = INVALID;
            }
        } else {
            this.birthDateError = null;
        }
    }

    mailValidateErrorMessage(email) {
        if (!email || email.trim().length == 0 || !this.validateEmail(email)) {
            if (!email) {
                this.emailError = REQUIRED
            } else {
                this.emailError = "Email incorrect. Veuillez le saisir à nouveau."
            }
        } else {
            this.emailError = null
        }
    }

    mobileValidateErrorMessage(mobilePhone) {
        if (!mobilePhone || mobilePhone.toString().trim().length == 0 || !this.validatePhone(mobilePhone)) {
            if (!mobilePhone) {
                this.mobilePhoneError = REQUIRED
            } else {
                this.mobilePhoneError = INVALID
            }
        } else {
            this.mobilePhoneError = null
        }
    }
    
    validate() {
        this.initErrors();
        var isValid : Boolean = true;
        if (!this.lastName || this.lastName.trim().length == 0) {
            this.lastNameError = REQUIRED
            isValid = false;
        }
        if (!this.firstName || this.firstName.trim().length == 0) {
            this.firstNameError = REQUIRED;
            isValid = false;
        }
        if (!this.validateDate(this.birthDate)) {
            if (!this.birthDate) {
                this.birthDateError = REQUIRED;
            } else {
                this.birthDateError = INVALID;
            }
            isValid = false;
        }
        if (!this.email || this.email.trim().length == 0 || !this.validateEmail(this.email)) {
            if (!this.email) {
                this.emailError = REQUIRED
            } else {
                this.emailError = "Email incorrect. Veuillez le saisir à nouveau."
            }
            isValid = false;
        }
        if (!this.mobilePhone || this.mobilePhone.toString().trim().length == 0 || !this.validatePhone(this.mobilePhone)) {
            if (!this.mobilePhone) {
                this.mobilePhoneError = REQUIRED
            } else {
                this.mobilePhoneError = INVALID
            }
            isValid = false;
        }

        return isValid;
    }
    intErrorsSecondPage(){
        this.birthNameError = null;
        this.zipCodeError = null;
    }

    validateZipCode(zipCode) {
        if(!zipCode){
            this.zipCodeError = REQUIRED;
        } else {
            this.zipCodeError = null;
        }
    }

    validateInformationsSecondPage(){
        this.intErrorsSecondPage();
        var isValid = true;
        console.log(this.familySituation,this.maritalStatus)
        if(!this.zipCode){
            this.zipCodeError = REQUIRED;
            isValid = false;
        }
        if(this.maritalStatus && this.maritalStatus == 'married' && this.shouldValidateMartialStatus){
            if(!this.birthName || this.birthName.trim().length == 0){
                this.birthNameError = REQUIRED;
                isValid = false;
            }
        }
        return isValid;
    }

}

class ContactDetails {
    mainResidenceAddress: string;
    mainResidenceAddressError: string;
    additionalAddress: string;
    zipCode: string;
    zipCodeError: string;
    city: string;
    cityError: string;
    country: string = "FR";
    mainResidenceStatus: string;
    livesAtThisAddressSince: Date;
    livesAtThisAddressSinceError: string;
    hasPreviousAddress = false;
    isPostalAddressSameAsResidenceAddress = 'same';
    reasonForCreatingHSBCAccount: string = 'r1';

    //Previous contact detail
    previousResidanceAdress: string;
    previousResidanceAdressError: string;
    previousAdditionalAddress: string;
    previousZipCode: string;
    previousZipCodeError: string;
    previousCity: string;
    previousCityError: string;
    previousCountry: string = 'FR';

    //Postal address
    postalAdress: string;
    postalAdressError: string;
    postalAdditionalAddress: string;
    postalZipCode: string;
    postalZipCodeError: string;
    postalCity: string;
    postalCityError: string;
    postalCountry: string = 'FR';

    initErrorrs(){
        this.mainResidenceAddressError = null;
        this.zipCodeError = null;
        this.cityError = null;
        this.livesAtThisAddressSinceError = null;
        this.previousResidanceAdressError = null;
        this.previousZipCodeError = null;
        this.previousCityError = null;
        this.postalAdressError = null;
        this.postalZipCodeError = null;
        this.postalCityError = null;
    }

    validateRequiredString(str: string){
        if(str && str.trim().length > 0){
            return true;
        }
        return false;
    }

    validatePostalCode(zipCode) {
        if(!this.validateRequiredString(zipCode)){
           this.zipCodeError = REQUIRED;
       } else {
           this.zipCodeError = null;
       }
    }

    validateCity(city) {
        if(!this.validateRequiredString(city)){
           this.cityError = REQUIRED;
        } else {
           this.cityError = null;
        }
    }

    validateResidenceAddress(mainResidenceAddress) {
        if(!this.validateRequiredString(mainResidenceAddress)){
           this.mainResidenceAddressError = REQUIRED;
       } else {
           this.mainResidenceAddressError = null;
       }
    }

    validate(){
        this.initErrorrs();
        var isValid = true;
       if(!this.validateRequiredString(this.mainResidenceAddress)){
           this.mainResidenceAddressError = REQUIRED;
           isValid = false;
       }
       if(!this.validateRequiredString(this.zipCode)){
           this.zipCodeError = REQUIRED;
           isValid = false;
       }
       if(!this.validateRequiredString(this.city)){
           this.cityError = REQUIRED;
           isValid = false;
       }
       if(!this.validateDate(this.livesAtThisAddressSince)){
           if(!this.livesAtThisAddressSince){
               this.livesAtThisAddressSinceError = REQUIRED;
           }else{
               this.livesAtThisAddressSinceError = INVALID;
           }
           isValid = false;
       }
       if(this.hasPreviousAddress){
           if(!this.validateRequiredString(this.previousResidanceAdress)){
               this.previousResidanceAdressError = REQUIRED;
               isValid = false;
           }
           if(!this.validateRequiredString(this.previousZipCode)){
               this.previousZipCodeError = REQUIRED;
               isValid = false;
           }
           if(!this.validateRequiredString(this.previousCity)){
               this.previousCityError = REQUIRED;
               isValid = false;
           }
       }
       if(this.isPostalAddressSameAsResidenceAddress == 'different'){
           if(!this.validateRequiredString(this.postalAdress)){
               this.postalAdressError = REQUIRED;
               isValid = false;
           }
           if(!this.validateRequiredString(this.postalZipCode)){
               this.postalZipCodeError = REQUIRED;
               isValid = false;
           }
           if(!this.validateRequiredString(this.postalCity)){
               this.postalCityError = REQUIRED;
               isValid = false;
           }
       }
        return isValid;
    }
    validateDate(date){
        if(date){
            let unixdate = date.getTime();
            let unixCurrent = new Date().getTime();
            return unixdate < unixCurrent
        }
        return false;
    }
}

