import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import { HeaderStateService, HEADER_STATE } from '../../services/header-state.service';
import { SharedModelService } from '../../services/shared-model.service';
@Component({
  selector: 'app-proof-upload-validation',
  templateUrl: './proof-upload-validation.component.html',
  styleUrls: ['./proof-upload-validation.component.css']
})
export class ProofUploadValidationComponent implements OnInit {

 identityUploaded: any;
 signatureUploaded: any;
 taxImpositionUploaded: any;

  /**
   * @constructor
   * Constructor of ProofUploadValidationComponent class
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {HeaderStateService} _headerSteps
   * @param {SharedModelService} _sharedModelService Model service
   * @param {FillDataStepsChangerService} _fillDataStepService 
   */
  constructor(private _location: Location,
    private router: Router,
    private _headerSteps: HeaderStateService,
    private _sharedModelService: SharedModelService,
    private _fillDataStepService: FillDataStepsChangerService) { }

/**
 * Initialisations
 * @function ngOnInit
 */
  ngOnInit() {
    this._fillDataStepService.setCurrentStep({step:-1,subStep:-1});
    this._headerSteps.setCurrentState(HEADER_STATE.UPLOAD_JUSTIFICATIFS);
  }

  /**
   * Move to the previous step
   * @function goBack
   */
  goBack() {
    this._location.back();
     window.scrollTo(0,0)
  }

  allFilesUploaded(){
    return this.identityUploaded && this.signatureUploaded && this.taxImpositionUploaded  ? null:true
  }

  /**
   * Move to the next step
   * @function next
   */
  next() {
    this.router.navigate(['account-opening-validation']);
     window.scrollTo(0,0)
  }

}



