import { Component, OnInit } from '@angular/core';
import {TranslateService} from 'ng2-translate';
import {Router}  from '@angular/router';
import {Location} from '@angular/common';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import { HeaderStateService, HEADER_STATE } from '../../services/header-state.service';
import {SharedModelService} from '../../services/shared-model.service';
import {SegmentSelectItem} from '../../components/segment-control.component';
@Component({
  selector: 'app-credit-card-second-page',
  templateUrl: './credit-card-second-page.component.html',
  styleUrls: ['./credit-card-second-page.component.css']
})
export class CreditCardSecondPageComponent implements OnInit {

  /**
   * @var {array} accountUsages
   * @var {string} accountUsage
   * @var {array} contactTypes
   * @var {string} selectedcontactType
   * @var {boolean} advantages
   * @var {boolean} domicilierVirements
   * @var {boolean} detenezVirementsPermanents
   * @var {boolean} transferCreditToNewHSBCAccount
   * @var {string} compteIBAN
   * @var {string} numberOfdaysToAccountToBeCanceled
   * @var {string} numberOfdaysToCreditBalanceToBeTransferred
   * @var {array} monthlyAmountsInTheNext12Months
   * @var {string} monthlyAmountInTheNext12Months
   */
  accountUsages = [{label:'Compte principal ', value:'cp'},{label:'Compte secondaire', value:'cs'}];
  accountUsage : string = "cp";
  contactTypes : SegmentSelectItem[] = [{label:'Téléphone mobile', icon:"icon-phone", value:'tel'},{label:'Email', icon:"icon-message", value:'email'}];
  selectedcontactType = 'tel';
  advantages : boolean = false;
  domicilierVirements : boolean = true;
  detenezVirementsPermanents : boolean;
  transferCreditToNewHSBCAccount : boolean;
  compteIBAN : string;
  numberOfdaysToAccountToBeCanceled : string;
  numberOfdaysToCreditBalanceToBeTransferred : string;
  monthlyAmountsInTheNext12Months=[{label:'Madame',value:'md'},{label:'Monsieur',value:'mo'}];
  monthlyAmountInTheNext12Months : string;


  /**
   * @constructor
   * Constructor of CreditCardSecondPageComponent class
   * @param {TranslateService} translate
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {HeaderStateService} _headerSteps
   * @param {SharedModelService} _sharedModelService Model service
   * @param {FillDataStepsChangerService} _fillDataStepService 
   */
  constructor(translate: TranslateService,private router: Router,private _location:Location,
    private _headerSteps: HeaderStateService,
    private _sharedModelService: SharedModelService,
    private _fillDataStepService: FillDataStepsChangerService) { }

/**
 *Initialisations
 * @function ngOnInit
 */
  ngOnInit() {
    this._fillDataStepService.setCurrentStep({step:FILL_DATA_STEPS.ACCOUNT,subStep:3});
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);
  }

/**
 * Discover advantages of the credit card
 * @function discoverBenefits
 */
  discoverBenefits() {
    this.advantages = !this.advantages;
  }

  /**
   * Move to the previous step
   * @function goBack
   */
  goBack(){
    this._location.back();
    window.scrollTo(0,0)
  }

  /**
   * Move to the next step
   * @function next
   */
  next(){
    this.router.navigate(['recap-validation']);
    window.scrollTo(0,0)
  }

}



