import { Component,NgModule, OnInit,Input } from '@angular/core';
import {CommonModule} from '@angular/common'
@Component({
  selector: 'help-button',
  template: `
    <div [ngClass]="{'infobulle':true}" [class]="containerClass">
        <i [ngClass]="{'icon':true, 
                       'icon-circle-help-solid':shouldShowBullOnCLick || shouldShowBullHover ,
                       'icon-circle-help':!(shouldShowBullOnCLick || shouldShowBullHover)}"
            [class]="helpIconClass"
            [ngStyle]="helpIconStyle"
            (mouseenter)="onMouseEnter()" 
            (mouseleave)="onMouseLeave()" 
            (click)="onClick()"></i>
        <div [ngClass]="{'infobulle__content':true,float_abs:floatAbs}" 
             [class]="tooltipClass">
            {{tooltipText}}
        </div>
     </div>
  `,
  styles:[
    `.float_abs{
      position: absolute;
      left: 0;
      text-align:left;
    }`
  ]
})
export class HelpButtonComponent {
  
  shouldShowBullHover: boolean = false;
  shouldShowBullOnCLick: boolean = false;
  @Input() helpIconClass: String  = "";
  @Input() helpIconStyle: string = "";
  @Input() showOnHover: boolean = true;
  @Input() showOnClick: boolean = true;
  @Input() tooltipText: string = "";
  @Input() tooltipClass: string= "";
  @Input() tooltipStyle: any;
  @Input() containerClass: string = "";
  @Input() floatAbs: boolean = false;

  onMouseEnter(){
      this.shouldShowBullHover = this.showOnHover;
  }
  onMouseLeave(){
      this.shouldShowBullHover = false;
  }
  onClick(){
    this.shouldShowBullOnCLick = !this.shouldShowBullOnCLick;
    this.shouldShowBullHover = false;
  }
}

@NgModule({
  imports: [CommonModule],
  exports: [HelpButtonComponent],
  declarations: [HelpButtonComponent]
})
export class PibHelpButtonModule { }



