import {Component, Input, forwardRef, NgModule, EventEmitter, Output, ViewChild, ElementRef} from '@angular/core';
import {ControlValueAccessor,AbstractControl, NgModel, NG_VALUE_ACCESSOR,NG_VALIDATORS, FormsModule} from "@angular/forms";
import {CommonModule} from "@angular/common";
import { PibTooltipModule } from './tooltip.directive';
import {CurrencyMaskModule} from './currency-mask/currency-mask.module';
import {GooglePlaceModule} from './google-place-autocomplete/ng2-google-place.module';
//function
const noop = (_?: any) => {};
@Component({
  selector: 'pib-text-field',
  template: `
    <div class="form-group" [class.has-error]="hasErrors()"
      [pibTooltip]="tooltipText"
      [tooltipPosition]="tooltipPosition">
      <label class="form__label" [attr.for]="name" *ngIf="label">{{label}}</label>
      <div style="position: relative;">
       <i [class]="'icon '+iconClass"   *ngIf="showIcon"
          [ngClass] = "{'left-icon':iconPlace == 'LEFT','right-icon':iconPlace == 'RIGHT'}"
          [style]="iconStyle"></i>
          <ng-container [ngSwitch]="inputType">
              
              <input  *ngSwitchCase="'currency'"
                      [class]="'form__input '+inputClass"
                      [ngClass]="{'input-left-icon':showIcon && iconPlace == 'LEFT',
                                'input-right-icon': showIcon && iconPlace == 'RIGHT',
                                'form__input--error':messageType == 'ERROR' && hasErrors(),
                                'form__input--warning':messageType == 'WARNING' && hasErrors()
                              }"
                      [type]="type"
                      currencyMask [options]="currencyOptions"
                      [(ngModel)]="value" 
                      #inputText = "ngModel"
                      [name]="name"
                      [attr.disabled] = "disabled ? '':null"
                      [placeholder]="placeholder"
                      (hover)="hover.emit($event)"
                      (focus)="focus.emit($event)"
                      (blur)="blur.emit($event)"/>
              <input *ngSwitchCase="'place'"
                      [class]="'form__input '+inputClass" 
                      type="text" size="100" 
                      [options]="placeOptions" 
                      [name]="name"
                      (setAddress) = "onAddress.emit($event)"
                      (street_number) = 'street_number.emit($event)'
                      (street)= "street.emit($event)"
                      (city)= "city.emit($event)"
                      (state)="state.emit($event)"
                      (district)="district.emit($event)"
                      (country)='country.emit($event)'
                      (country_code)='country_code.emit($event)'
                      (postal_code)="postal_code.emit($event)"
                      (lat)="lat.emit($event)" 
                      (lng)="lng.emit($event)" 
                      (adr_address)="adr_address.emit($event)"
                      (formatted_address)="formatted_address.emit($event)"
                      (name)="addressName.emit($event)"
                      (place_id)="place_id.emit($event)" 
                      (types)="types.emit($event)" 
                      (url)="url.emit($event)"  
                      (utc_offset)="utc_offset.emit($event)"
                      (vicinity)="vicinity.emit($event)"
                      (photos)="photos.emit($event)"
                      (airport)="airport.emit($event)" 
                      (CountryCodes)="CountryCodes.emit($event)"
                      [(ngModel)]="value" 
                      #inputText = "ngModel"
                      [attr.disabled] = "disabled ? '':null"
                      [placeholder]="placeholder"
                      ng2-google-place-autocomplete
                      (focus)="focus.emit($event)"
                      (blur)="blur.emit($event)"
                      (hover)="hover.emit($event)"/> 


               <input *ngSwitchDefault
                      [class]="'form__input '+inputClass"
                      [ngClass]="{'input-left-icon':showIcon && iconPlace == 'LEFT',
                                  'input-right-icon': showIcon && iconPlace == 'RIGHT',
                                  'form__input--error':messageType == 'ERROR' && hasErrors(),
                                  'form__input--warning':messageType == 'WARNING' && hasErrors()
                                }"
                      [type]="type"
                      [(ngModel)]="value" 
                      #inputText = "ngModel"
                      [attr.disabled] = "disabled ? '':null"
                      [name]="name"
                      [placeholder]="placeholder"
                      (hover)="hover.emit($event)"
                      (focus)="focus.emit($event)"
                      (blur)="blur.emit($event)"/>
                    
        </ng-container>
      
     
      </div>
      <div class="form__message" [ngClass] = "{'form__message--error':messageType == 'ERROR',
                                              'form__message--warning':messageType == 'WARNING'}"
                                  *ngIf="hasErrors() && showMessage">
          <i class="icon" [ngClass] = "{'icon-circle-delete' :messageType == 'ERROR',
                                        'icon-circle-error': messageType == 'WARNING'}"></i>
                                  {{message}}
      </div>
    </div>
`,
  styles:[
    `
      .left-icon{
        position: absolute;
        top: 8px;
        left: 5px;
        z-index: 0;
        pointer-events:none;
        font-size: 24px;
      }
      .right-icon{
        position: absolute;
        top: 8px;
        right: 5px;
        z-index: 0;
        pointer-events:none;
        font-size: 24px;
      }
      .input-left-icon{
        padding-left: 30px;
      }
      .input-right-icon{
        padding-right: 30px;
      }
    `
  ],
  providers: [
    { provide: NG_VALUE_ACCESSOR, useExisting: forwardRef(() => PibTextField), multi: true },
     { provide: NG_VALIDATORS, useExisting: forwardRef(() => PibTextField), multi: true }
  ]
})
export class PibTextField implements ControlValueAccessor {
  protected onModelChange: (_: any) => void = noop;
  protected onModelTouched: () => void = noop;
  protected _value: any;
  @Output() public change: EventEmitter<any> = new EventEmitter();//event fired when the model (text in input) change
  @Output() public blur: EventEmitter<any> = new EventEmitter();//event fired when the input get blurred
  @Output() public focus: EventEmitter<any> = new EventEmitter();//event fired when the input get focused
  @Output() public hover: EventEmitter<any> = new EventEmitter();//event fired when the input get hovered

//place events
  @Output() public street_number: EventEmitter<any> = new EventEmitter(); // emit the street number
  @Output() public postal_code: EventEmitter<any> = new EventEmitter();
  @Output() public onAddress: EventEmitter<any> = new EventEmitter();
  @Output() public street: EventEmitter<any> = new EventEmitter();
  @Output() public city: EventEmitter<any> = new EventEmitter();
  @Output() public state: EventEmitter<any> = new EventEmitter();
  @Output() public district: EventEmitter<any> = new EventEmitter();
  @Output() public country: EventEmitter<any> = new EventEmitter();
  @Output() public country_code: EventEmitter<any> = new EventEmitter();
  @Output() public adr_address: EventEmitter<any> = new EventEmitter();
  @Output() public lat: EventEmitter<any> = new EventEmitter();
  @Output() public lng: EventEmitter<any> = new EventEmitter();
  @Output() public addressName: EventEmitter<any> = new EventEmitter();
  @Output() public place_id: EventEmitter<any> = new EventEmitter();
  @Output() public types: EventEmitter<any> = new EventEmitter();
  @Output() public url: EventEmitter<any> = new EventEmitter();
  @Output() public utc_offset: EventEmitter<any> = new EventEmitter();
  @Output() public vicinity: EventEmitter<any> = new EventEmitter();
  @Output() public photos: EventEmitter<any> = new EventEmitter();
  @Output() public airport: EventEmitter<any> = new EventEmitter();
  @Output() public CountryCodes: EventEmitter<any> = new EventEmitter();
  @Output() public formatted_address: EventEmitter<any>  = new EventEmitter();
//place input 
@Input() public placeOptions: any;


  @Input() public placeholder: string = '';
  @Input() public label: string;

  @Input() public currencyOptions = { prefix: '',suffix:'€', thousands: '', decimal: '',align:'left',precision:0}
  @Input() public message: any;
  @Input() public type : string = 'text';
  @Input() public messageType: string = 'ERROR';
  @Input() public disabled : boolean = false;
  @Input() public showIcon : boolean = false;
  @Input() public showMessage: boolean = true;
  @Input() public iconPlace : string = 'LEFT';
  @Input() public iconClass: string = '';
  @Input() public iconStyle: string = '';
  @Input() public inputStyle: string = '';
  @Input() public inputClass: string = '';
  @Input() public tooltipPosition: string;
  @Input() public tooltipText: string ;
  @Input() public inputType: string = 'text';//place, currency
  @Input() public name:string = ''
  @ViewChild('inputText') inputTf: ElementRef;
  constructor() { }

  /**
   * get the input value
   * @returns {any}
   */
  get value(): any {
    return this._value;
  }

  /**
   * set the input text
   * @param value
   */
  set value(value: any) {
    if (value !== this._value) {
      this._value = value;
      this.change.emit({value:value});
      this.onModelChange(value)
    }
  }

  /**
   * write value (from interface ControlValueAccessor)
   * @param value
   */
  public writeValue(value: any) {
    if (value !== this._value) {
      this._value = value;
    }
  }

  /**
   * register onChange event
   * @param fn
   */
  public registerOnChange(fn: (_: any) => void) {
    this.onModelChange = fn;
  }

  /**
   * register onTouch event
   * @param fn
   */
  public registerOnTouched(fn: () => void) {
    this.onModelTouched = fn;
  }

  /**
   * return true if the input has errors
   * @returns {boolean}
   */
  public hasErrors(): boolean {
    return this.message != null && this.message.trim().length > 0;
  }

  /**
   * blur the input
   */
  public blurTextField(){
    this.inputTf.nativeElement.blur()
  }

  /**
   * focus the input
   */
  public focusTextField(){
    this.inputTf.nativeElement.focus()
  }
  /**
   * validate the form control return null when internal value is not null else invalidate 
   * @param c 
   */
   validate(c: AbstractControl) {
    if (!this._value) {
      return { invalidDate: true };
    }
    return null;
  }
}

@NgModule({
  imports: [CommonModule,FormsModule,PibTooltipModule,CurrencyMaskModule,GooglePlaceModule],
  exports: [PibTextField],
  declarations: [PibTextField]
})
export class PibTextFieldModule { }



